.btn {
  background: #08A6E4;
  padding: 22px 157px;
  box-shadow: 5px 10px 14px #07ACE629;
  border-radius: 40px;
  text-decoration: none;
  color: #FFF;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}
@media (max-width: 767px) {
  .btn {
    padding: 22px 92px;
    display: block;
    margin: 0 15px;
  }
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 1352px;
  margin: 0 auto;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}

.header {
  left: 0;
  right: 0;
  top: 0;
  border-bottom: 1px solid #A5A5A5;
  box-shadow: 0 3px 6px #A5A5A5;
  z-index: 1;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header nav {
  display: flex;
}
@media (max-width: 767px) {
  .header nav {
    display: none;
    position: absolute;
    left: 0;
    top: 70px;
    flex-direction: column;
    box-shadow: 0 3px 6px #A5A5A5;
    width: 100%;
    padding: 20px 0;
  }
}
.header nav a {
  font-family: "Open Sans", sans-serif;
  font-size: 0.6875rem;
  font-weight: 700;
  letter-spacing: 0.1875rem;
  color: #A5A5A5;
  text-decoration: none;
  text-transform: uppercase;
  padding: 28px 10px;
  margin: 0 40px;
  border-bottom: 4px solid transparent;
}
@media (min-width: 768px) {
  .header nav a:hover {
    color: #000;
    border-color: #08A6E4;
  }
}
@media (max-width: 767px) {
  .header nav a {
    padding: 14px 0;
  }
  .header nav a:active {
    color: #000;
  }
}

.logo {
  font-family: Raleway, sans-serif;
  font-size: 2rem;
  font-weight: 300;
  color: #08A6E4;
  text-decoration: none;
}

.menu_btn {
  display: none;
  position: absolute;
}
@media (max-width: 767px) {
  .menu_btn:checked ~ .header_nav {
    display: flex;
  }
  .menu_btn:checked ~ .menu_burger span:first-child {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .menu_btn:checked ~ .menu_burger span:nth-child(2) {
    opacity: 0;
  }
  .menu_btn:checked ~ .menu_burger span:last-child {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}

@media (max-width: 767px) {
  .menu_burger {
    margin: 29px 30px 26px 0;
  }
  .menu_burger span {
    display: block;
    width: 20px;
    height: 2px;
    background: #08A6E4;
    margin-bottom: 3px;
  }
}
