  background: url("../assets/Background.png") no-repeat bottom;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
}
.opening h1 {
  font-family: Raleway, sans-serif;
  font-size: 3.0625rem;
  margin-bottom: 20px;
  margin-top: 200px;
}
.opening p {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  margin-bottom: 60px;
}
@media (max-width: 767px) {
  .opening p {
    padding: 0 30px;
  }
}

.statistics {
  text-align: center;
  margin-bottom: 120px;
}
.statistics h2 {
  font-family: Raleway, sans-serif;
  font-size: 2.25rem;
  margin: 100px 0;
}
@media (max-width: 767px) {
  .statistics h2 {
    font-size: 1.875rem;
    line-height: 2.1875rem;
    padding: 0 15px;
    margin-bottom: 25px;
  }
}
.statistics__content {
  display: flex;
  justify-content: space-around;
}
@media (max-width: 767px) {
  .statistics__content {
    flex-direction: column;
    align-items: center;
  }
}
.statistics__box {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
}
@media (max-width: 767px) {
  .statistics__box {
    margin-bottom: 25px;
  }
}
.statistics__box h3 {
  font-family: Raleway, sans-serif;
  font-weight: 800;
  font-size: 1.25rem;
  letter-spacing: 0.375rem;
  padding: 15px 0;
}
.statistics__box p {
  font-family: Raleway, sans-serif;
  font-size: 1.0625rem;
  color: #A5A5A5;
}
.statistics img {
  position: absolute;
}
.statistics__img-1 {
  top: 5px;
  right: 35px;
}
.statistics__img-2 {
  top: 15px;
  right: 60px;
}
.statistics__img-3 {
  top: 5px;
  right: 40px;
}

